.headtitle {
	@include fs(36rem);
	font-weight: 500;
	text-transform: uppercase;
	@include media-breakpoint-down(lg) {
		@include fs(28rem); }
	@include media-breakpoint-down(xs) {
		@include fs(24rem); } }
.headdes {
	@include fs(18rem);
	font-weight: 400;
	@include media-breakpoint-down(lg) {
		@include fs(16rem); }
	@include media-breakpoint-down(xs) {
		@include fs(14rem); } }
.home-event {
	height: 100%;
	margin-bottom: 30px;
	.event {
		height: 100%;
		figure {
			height: 100%;
			@include pos(rel,null);
			overflow: hidden;
			.eventimg {
				display: block;
				height: 250px; }
			figcaption {
				@include pos(abs, null 0 0 0);
				padding: 30px 15px 15px;
				@include transform(translateY(calc(100% - 90px)));
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5));
				@include transition(all,0.3s);
				.eventname {
					@include fs(20rem);
					font-weight: 500;
					background: $pink;
					padding: 10px;
					display: inline-block;
					margin-bottom: 15px;
					a {
						color: $white; } }
				.eventdes {
					color: $white;
					@include fs(16rem);
					@include transform(translateY); } }
			&:hover {
				figcaption {
					@include transform(translateY(0%)); } } } } }
.home-ads {
	margin-bottom: 30px;
	@include media-breakpoint-down(sm) {
		margin-bottom: 0; }
	.banner-sale {
		height: 100%;
		@include media-breakpoint-down(sm) {
			margin-bottom: 30px;
			height: unset; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 10px; }
		.slick-list, .slick-track {
			height: 100%;
			@include media-breakpoint-down(sm) {
				height: unset; } }
		.item {
			height: 100%;
			@include media-breakpoint-down(sm) {
				height: unset; } }
		a {
			display: block;
			height: 200px;
			@include media-breakpoint-down(md) {
				height: 100px; }
			@include media-breakpoint-down(sm) {
				height: 180px; }
			@include media-breakpoint-down(xs) {
				height: 110px; } } } }
.home-news {
	@include flexbox;
	flex-wrap: wrap;
	margin-bottom: 30px;
	background: $pink;
	@include media-breakpoint-down(xs) {
		margin-bottom: 10px; }
	.label {
		text-transform: uppercase;
		color: $white;
		font-weight: 500;
		line-height: 40px;
		padding: 0px 20px;
		width: 100px; }
	.TickerNews {
		width: calc(100% - 100px); }
	.ti_wrapper {
		width: 100%;
		position: relative;
		overflow: hidden; }
	.ti_slide {
		width: 30000px;
		position: relative;
		left: 0;
		top: 0;
 }		// white-space: nowrap
	.ti_content {
		width: 8000px;
		position: relative;
		// white-space: nowrap
		float: left; }
	.newsitem {
		float: left;
		padding: 10px 0; }
	.newsitem a {
		display: block;
		color: $white;
		padding: 0px 10px;
		line-height: 20px;
		border-left: 1px solid color-rgba($white,0.5); } }
.hot-product {
	padding: 15px;
	margin-bottom: 30px;
	@include media-breakpoint-down(lg) {
		padding: 10px; }
	.headtitle {
		margin: 10px 0 5px; }
	.headdes {
		margin-bottom: 20px; }
	.headtitle, .headdes {
		text-align: center;
		color: $white;
		text-shadow: 1px 1px 1px color-rgba($black,0.25); }
	.hotproduct-slide {
		overflow: hidden;
		.item {
			padding: 0.5px; } } }

.product-category {
	margin-bottom: 30px;
	.category-img {
		display: block;
		width: 33.33%;
		padding: 50px 25px;
		color: $black;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			width: 21%;
			padding: 15px; }
		@include media-breakpoint-down(md) {
			width: 100%;
			padding: 80px 0;
 }			// height: 100px
		@include media-breakpoint-down(xs) {
			padding: 30px 0; }
		.category-overlay {
			@include pos(abs, 50% null 0 0);
			right: 0;
			background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5));
			// opacity: 0.3
			z-index: 1;
			// display: none
			@include media-breakpoint-down(md) {
				display: block; } }
		.category-name {
			@include fs(24rem);
			text-transform: uppercase;
			font-weight: 700;
			// opacity: 0.65
			margin-bottom: 5px;
			@include transition(all,0.3s);
			@include pos(abs,null 20px 15px 20px);
			z-index: 2;
			color: $white;
			@include media-breakpoint-down(lg) {
				@include fs(20rem); }
			@include media-breakpoint-down(md) {
				opacity: 1;
				text-align: center; }
			&:after {
				content: "\e876";
				font-family: 'linearicons-free';
				margin-left: 10px; } }
		.amount {
			@include fs(16rem);
			opacity: 0.65;
			@include pos(rel,null);
			z-index: 1;
			display: none;
			@include media-breakpoint-down(lg) {
				@include fs(14rem); }
			@include media-breakpoint-down(md) {
				color: $white;
				// opacity: 1
				text-align: center; } }
		&:hover {
			.category-name {
				letter-spacing: 1px; } } }
	.product-list {
		width: calc(100% - 33.33%);
		background: $white;
		@include media-breakpoint-down(lg) {
			width: calc(100% - 21%); }
		@include media-breakpoint-down(md) {
			width: 100%; } }
	.category-link {
		white-space: nowrap;
		border-bottom: 1px solid darken($white,10%);
		overflow: auto;
		background: $white;
		ul {
			li {
				@include fs(15rem);
				line-height: 34px;
				display: inline-block;
				padding: 0px 15px;
				a {
					display: block;
					color: $main;
					&:hover {
						color: $blue; } } } } }
	.category-slide, .category-slide1 {
		overflow: hidden;
		.item {
			border-left: 1px solid darken($white,10%);
			margin-left: -1px; } }
	.row {
		margin: 0px -5px; }
	.col-xs-12 {
		padding: 0px 5px; }
	.category-col {
		margin-bottom: 10px;
		figure {
			@include pos(rel,null);
			@include bor-rad(5px);
			overflow: hidden;
			.img {
				display: block;
				@include transition(all,0.3s);
				img {
					width: 100%;
					@include bor-rad(5px); } }
			figcaption {
				@include pos(abs, 50% 0 0 0);
				padding: 5px 10px;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5));
				color: $black;
				.name {
					@include fs(16rem);
					text-transform: uppercase;
					font-weight: 700;
					float: left;
					@include pos(abs, null 10px 10px 10px);
					color: $white;
					@include media-breakpoint-down(lg) {
						float: none;
						@include fs(15rem); }
					a {
						color: $white; }
					&:hover {
						letter-spacing: 1px; }
					&:after {
						content: "\e876";
						font-family: 'linearicons-free';
						margin-left: 5px; } }
				.amount {
					@include fs(14rem);
					font-weight: 400;
					opacity: 0.65;
					float: right;
					@include media-breakpoint-down(lg) {
						float: none;
						@include fs(13rem); } } }
			&:hover {
				box-shadow: 2px 2px 5px color-rgba($black,0.1);
				figcaption {
					.name {
						letter-spacing: 1px; } } } } } }
