// CART HEADER

.cartheader {
	background: lighten($yellow,00%);
	border-top: 5px solid $red;
	border-bottom: 1px solid darken($white,20%);
	// margin-bottom: 40px
	.container {
		// max-width: unset
		// width: 100%
		// padding: 0px 50px
		// +media-breakpoint-down(lg)
 }		// 	padding: 0px 15px
	.sitelogo {
		margin: 10px 0;
		float: left;
		a {
			display: block;
			img {
				height: 60px; } } }
	.hotline {
		float: right;
		padding: 5px;
		// +bor-rad(50px)
		// border: 2px solid color-rgba($white,0.5)
		margin: 10px 0;
		@include bor-rad(5px);
		background: color-rgba($red,0.7);
		@include media-breakpoint-down(xs) {
			display: none; }
		.fa {
			display: block;
			width: 40px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(24rem);
			@include bor-rad(50px);
			float: left;
			background: $white;
			color: $green; }
		.text {
			float: left;
			padding: 0 10px;
			line-height: 1.3;
			color: color-rgba($white,0.8);
			a {
				display: block;
				font-weight: bold;
				color: $white;
				&:hover {
					color: $green; } } } }
	.cartdes {
		float: right;
		clear: right;
		@include fs(14rem);
		color: darken($white,40%);
		@include media-breakpoint-down(xs) {
			display: none; }
		a {
			color: $white;
			&:hover {
				color: $green; } } } }
