.brand-group {
	@include media-breakpoint-down(md) {
		display: none; } }
.brand-logo {
	display: flex;
	height: 150px;
	justify-content: center;
	align-items: center;
	// border-top: 1px solid darken($white,10%)
	img {
		max-width: 80%;
		max-height: 80%; } }
.btn-togglewrap {
	@include pos(fix, 60px 0 null 0);
	z-index: 80;
	background: $red;
	display: flex;
	div {
		+ div {
			border-left: 1px solid lighten($red,10%); } }
	.btn-showcate, .btn-showfilter {
		text-align: center;
		width: 100%;
		background: $red;
		color: $white;
		padding: 0 20px;
		// margin-bottom: 20px
		line-height: 36px;
		display: none;
		@include fs(14rem);
		font-weight: 500;
		// border-right: 1px solid lighten($red,10%)
		// text-transform: uppercase
		@include transition(all,0.3s);
		@include media-breakpoint-down(md) {
			display: block; }
		@include media-breakpoint-down(xs) {
			@include fs(12rem);
			padding: 0px 10px; }
		&:hover, &.active {
			background: darken($red,10%); } }
	.btn-showcate {
		&:before {
			content: '\f00b';
			font-family: 'fontawesome';
			margin-right: 10px;
			// +fs(18rem)
			display: inline-block;
			vertical-align: middle;
			@include media-breakpoint-down(xs) {
				// +fs(15rem)
				margin-right: 5px; } } }
	.btn-showfilter {
		&:before {
			content: '\f013';
			font-family: 'fontawesome';
			margin-right: 10px;
			// +fs(18rem)
			display: inline-block;
			vertical-align: middle;
			@include media-breakpoint-down(xs) {
				// +fs(15rem)
				margin-right: 5px; } } } }
.product-sidebar {
	background: $white;
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
 } }		// z-index: 150
.btn-closefilter {
	@include pos(abs, 30px 30px null null);
	@include fs(24rem);
	color: $main;
	@include transition(all,0.3s);
	line-height: 1;
	display: none;
	&:hover {
		color: $red; }
	@include media-breakpoint-down(md) {
 }		// display: block
	@include media-breakpoint-down(sm) {
		top: 15px;
		right: 15px; }
	@include media-breakpoint-down(xs);
	&.btn-close {
		@include pos(static,unset);
		border: 1px solid darken($white,20%);
		@include fs(14rem);
		text-align: center;
		padding: 8px 0;
		@include bor-rad(3px);
		@include media-breakpoint-down(md) {
			display: block; }
		&:hover {
			color: $red;
			border-color: $red;
			background: lighten($red,45%); } } }
.filter-wrap {
	// background: $white
	// margin-bottom: 20px
	@include media-breakpoint-down(md) {
		@include pos(fix, 96px null 0 0);
		z-index: 70;
		right: 0;
		background: $white;
		overflow: auto;
		padding: 30px 200px;
		@include transform(translateY(-130%));
		@include transition(all,0.5s);
		// opacity: 0
		visibility: hidden;
		&.open {
			@include transform(translateY(0%));
			// opacity: 1
			visibility: visible; } }
	@include media-breakpoint-down(sm) {
		padding: 30px 100px; }
	@include media-breakpoint-down(xs) {
		padding: 15px; } }
.filter-group {
	// border-top: 2px solid $red
	// background: $white
	// margin-bottom: 30px
	@include media-breakpoint-down(md) {
 }		// margin-bottom: 15px
	&:before, &:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; }
	.btn-reset {
		@include pos(abs, 6px 5px null null);
		width: 30px;
		height: 30px;
		line-height: 30px;
		@include fs(12rem);
		text-align: center;
		cursor: pointer;
		@include transition(all,0.3s);
		color: darken($white,40%);
		&:hover {
			color: $red; } } }
.filter-title {
	font-weight: 600;
	padding: 10px 0;
	color: $red;
	@include fs(14rem);
	// border-bottom: 1px solid darken($white,10%)
	@include pos(rel,null);
	span {
		display: inline-block;
		margin-bottom: -2px;
		font-weight: 600;
 } }		// text-transform: uppercase
.filter-option {
	background: lighten($yellow,35%);
	@include bor-rad(5px);
	overflow: hidden; }
.filter-category {
	> ul {
		> li {
			// text-transform: uppercase
			@include pos(rel,null);
			> a {
				display: block;
				padding: 7px 15px;
				color: $main;
				@include fs(14rem);
				// font-weight: 500
				&:hover {
					color: $red; } }
			&.active {
				font-weight: 500;
				&:before {
					opacity: 1; }
				> a {
					border-bottom: none;
					color: $white;
					background: $red; }
				.btn-showsubfilter {
					@include transform(scale(1,-1));
					color: $white;
					&.active {
						@include transform(scale(1,1)); } }
				.sub {
					display: block; } }
			.btn-showsubfilter {
				@include pos(abs, 5px 0 null null);
				width: 30px;
				text-align: center;
				cursor: pointer;
				@include transition(all,0.3s);
				&.active {
					@include transform(scale(1,-1)); } }
			.sub {
				display: none;
				// padding: 5px 0
				padding-left: 15px;
				// background: lighten($yellow,30%)
				li {
					font-weight: normal;
					text-transform: none;
					padding:  0px 0px;
					@include fs(13rem);
					@include media-breakpoint-down(lg);
					a {
						display: block;
						padding: 5px 15px;
						@include pos(rel,null);
						color: $main;
						&:before {
							content: '\f101';
							font-family: 'fontawesome';
							// margin-left: 10px
							@include pos(abs, 5px null null 0);
							// display: block
							// width: 14px
							// height: 14px
							// +pos(abs, 7px null null 0px)
							// +bor-rad(3px)
 }							// border: 1px solid darken($white,20%)
						// &:after
						// 	content: '\f00c'
						// 	font-family: 'fontawesome'
						// 	+fs(12rem)
						// 	// color: darken($white,20%)
						// 	color: $white
						// 	+pos(abs, 5px null null 2px)
						// 	opacity: 0
 }						// 	font-weight: normal
					&:hover {
						> a {
							color: $red; } }
					&.active {
						> a {
							color: $red;
							font-weight: 700;
							&:before {
								// background: $red
 }								// border-color: $red
							&:after {
								opacity: 1; } } } } } } } }

.sidebar-category {
	@extend .filter-category;
	ul {
		li {
			font-weight: normal;
			a {} } } }


.brand-category {
	padding: 10px 0;
	> ul {
		li {
			font-weight: normal;
			text-transform: none;
			padding:  0px 15px;
			a {
				display: block;
				padding: 5px 20px;
				@include pos(rel,null);
				color: $main;
				@include fs(14rem);
				&:before {
					content: '';
					display: block;
					width: 14px;
					height: 14px;
					@include pos(abs, 7px null null 0px);
					@include bor-rad(2px);
					border: 1px solid darken($white,20%); }
				&:after {
					content: '\f00c';
					font-family: 'fontawesome';
					@include fs(12rem);
					// color: darken($white,20%)
					color: $white;
					@include pos(abs, 5px null null 2px);
					opacity: 0;
					font-weight: normal; }
				&:hover {
					color: $red; }
				&.active {
					color: $main;
					font-weight: 700;
					&:before {
						background: $red;
						border-color: $red; }
					&:after {
						opacity: 1; } } } } } }

.product-size {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	padding: 15px 15px 10px;
	.size, li {
		// display: inline-block
		// vertical-align: middle
		margin-right: 5px;
		margin-bottom: 5px;
		a {
			display: block;
			// width: 40px
			min-width: 30px;
			line-height: 28px;
			padding: 0px 5px;
			// +bor-rad(3px)
			@include fs(13rem);
			border: 1px solid darken($white,20%);
			// +bor-rad(3px)
			text-align: center;
			// background: $white
			@include transition(all,0.5s);
			cursor: pointer;
			color: darken($white,40%);
			font-weight: bold;
			// + .size
			// 	margin-left: 5px
			&:hover {
				color: $red; }
			&.active {
				border-color: $red;
				color: $red; } } } }
.color-picker {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	padding: 15px;
	.color {
		margin-right: 5px;
		margin-bottom: 5px;
		a {
			display: block;
			border: 1px solid darken($white,20%);
			padding: 3px;
			background: darken($white,3%);
 }			// +bor-rad(3px)
		.color-img {
			width: 22px;
			height: 22px;
			img {
				border: 2px solid darken($white,20%); } }
		a {
			&.active {
				border-color: $red; } } } }
.filter-range {
	// width: 100%
	margin: 0 25px;
	padding: 15px 0;
	@include pos(rel,null);
	&:before, &:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; }
	#slider-range {
		width: 100%;
		height: 4px;
		background: darken($white,10%);
		margin: 15px 0 15px;
		@include pos(rel,null);
		@include transition(all,0s); }
	.ui-widget-header {
		background: $red;
		position: absolute;
		z-index: 1;
		display: block;
		top: 0px;
		height: 4px;
		@include transition(all,0s); }
	.ui-slider-handle {
		position: absolute;
		z-index: 2;
		cursor: default;
		touch-action: none;
		top: 2px;
		margin-left: -0.6em;
		left: 15%;
		width: 14px;
		height: 14px;
		@include bor-rad(50%);
		background: $white;
		border: 2px solid $red;
		z-index: 1;
		cursor: pointer;
		@include transition(all,0s);
		@include transform(translateY(-50%));
		&:hover {
			background: $red; } }
	.min, .max {
		width: 16px;
		height: 16px;
		background: darken($white,60%);
		box-shadow: 0px 0px 5px -1px rgba(0,0,0,.7);
		z-index: 1;
		cursor: pointer;
		@include transition(all,0.5s);
		@include transform(translateY(-50%));
		&:hover {
			background: $main; } }
	.min {
		@include pos(abs, 50% null null 0); }
	.max {
		@include pos(abs, 50% 0 null null); } }
.price-range {
	// text-transform: uppercase
	@include fs(13rem);
	// font-weight: bold
	margin-left: -10px; }
.min-input, .max-input {
	display: inline-block;
	margin: 0 5px;
	font-weight: bold;
	@include fs(14rem);
	color: $red; }

.product-sort {}
