// LOGIN, SIGNUP, RECOVER PW

.login-panel {
	background: $white;
	padding: 0 15px;
	margin-bottom: 40px;
	box-shadow: 0px 3px 30px color-rgba($black,0.1);
	@include bor-rad(5px);
	border: 1px solid darken($white,10%); }
.signup-form, .login-form {
	width: 100%;
	.headtitle {
		margin-bottom: 10px;
		color: $cyan;
		@include fs(15rem);
		line-height: 30px;
		font-weight: 700;
		border-bottom: 1px solid darken($white,10%); }
	.panel-heading {
		margin-bottom: 5px;
		color: darken($white,60%); }
	.form-group {
		margin-bottom: 10px;
		label {
			@include fs(14rem);
			margin-bottom: 5px;
			line-height: 36px; }
		input, select {
			width: 100%;
			@include bor-rad(3px);
			font-size: 0.9rem;
			border: 1px solid darken($white,15%);
			padding: 0px 15px;
			line-height: 36px;
			&:hover, &:active, &:focus {
				border-color: darken($white,30%); } }
		select {
			padding: 10px 15px; }
		.fa.fa-exclamation {
			top: 42px;
			right: 10px; } }
	.btnwrap {
		text-align: center;
		padding: 10px 0; }
	input[type="submit"] {
		display: block;
		width: 100%;
		line-height: 36px;
		padding: 0px 20px;
		@include bor-rad(0px);
		background: $red;
		color: $white;
		font-weight: 300 !important;
		@include fs(14rem);
		border: none;
		text-transform: uppercase;
		&:hover {
			background: darken($red,10%); } }
	.social-login {
		background: darken($white,5%);
		padding: 10px;
		margin: 0px -15px;
		.des {
			font-weight: 700;
			color: $main; }
		.social {
			a {
				display: inline-block;
				+ a {
					margin-left: 5px; }
				.fa {
					display: block;
					width: 36px;
					line-height: 36px;
					height: 36px;
					text-align: center;
					color: $white;
					&.fa-facebook {
						background: $facebook; }
					&.fa-google-plus {
						background: $google; }
					&.fa-yahoo {
						background: $yahoo; } } } } } }
html body .RadInput_Default .riTextBox {
	width: 100% !important;
	@include bor-rad(3px);
	border: 1px solid darken($white,15%) !important;
	// padding: 10px 15px !important
	padding: 0px 15px !important;
	line-height: 40px !important;
	font-size: 0.9rem !important; }

.login-form {
	.form-group {
		width: 100%;
		@include pos(rel,null);
		label {
			width: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(18rem);
			color: $red;
			@include pos(abs, 0 null null 0);
			.fa {
				color: $red !important;
				&:before {
					line-height: 1; } } }
		input {
			padding-left: 40px; }
		a {
			color: darken($white,60%);
			margin-right: 10px;
			&:hover {
				color: $blue; } }
		&.forget {
			@extend .check-group;
			label {
				width: auto;
				line-height: 1.5;
				@include fs(13rem);
				@include pos(static,null);
				color: darken($white,60%);
				&:hover {
					color: $blue; } } } }

	input[type="submit"] {
		padding: 0px 20px;
		border: none; }
	.btn-login {
		text-align: center;
		background: none;
		&:before {
			display: none; } } }

.signup-panel {
	background: $white;
	margin-bottom: 40px;
	.col-xs-12 {
		padding: 0 !important; } }
.signup-form {
	padding: 0px 15px;
	.form-group {
		@include flexbox;
		label {
			margin-bottom: 0;
			@include fs(14rem);
			width: 140px;
			line-height: 30px; }
		.settingcontrol {
			width: calc(100% - 130px);
			line-height: 30px;
			input {
				line-height: 30px;
				padding: 0px 10px; }
			select {
				padding: 6px 10px; } } }
	.gender-select {
		ul {
			margin-top: 3px;
			li {
				display: inline-block;
				label {
					line-height: 1.5; } } } }
	.checkbox-button {
		label {
			width: auto;
			line-height: 1.5; } }
	.btnwrap {
		margin-bottom: 15px; } }
.signupnote {
	height: 100%;
	background: $red; }
// USER SIDEBAR

.userpage {
	// .row
	// 	margin: 0px -5px
	// .col-xs-12
 }	// 	padding: 0px 5px
.user-sidebar {
	@include pos(rel,null);
	margin-top: 0;
	margin-bottom: 10px;
	@include media-breakpoint-down(md) {
		&:after {
			content: '\f107';
			font-family: 'fontawesome';
			@include pos(abs, 50% 15px null null);
			@include transform(translateY(-50%));
			color: $red;
			@include fs(24rem); } }
	.userinfo {
		border: 1px dashed $yellow;
		background: lighten($yellow,35%);
		@include bor-rad(5px);
		overflow: hidden;
		figure {
			&:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			@include flexbox;
			flex-wrap: wrap;
			.user-img {
				// display: none
				width: 80px;
				float: left;
				padding: 15px;
				@include media-breakpoint-down(lg) {
					width: 60px; } }
			figcaption {
				width: calc(100% - 80px);
				@include flexbox;
				flex-wrap: wrap;
				align-items: center;
				align-content: center;
				@include media-breakpoint-down(lg) {
					padding: 7px 15px; }
				.title {
					color: $main;
					opacity: 0.8;
					font-weight: 400;
					@include fs(13rem);
					width: 100%; }
				.user-name {
					text-transform: uppercase;
					font-weight: 800;
					// +fs(16rem)
					color: $red;
					width: 100%; }
				.user-rank {
					@include fs(13rem);
					font-weight: bold;
					color: $bluegrey;
					.fa {
						color: $amber;
						// margin-right: 5px
						@include fs(16rem); } } } }
		.btn-muasi {
			margin: 0px 15px 15px;
			display: block;
			background: $yellow;
			text-align: center;
			text-transform: uppercase;
			color: $red;
			font-weight: 700;
			@include bor-rad(5px);
			padding: 5px 0;
			box-shadow: 0 3px 10px color-rgba($black,0.3); } } }
.user-sidelink {
	background: darken($white,0%);
	border: 1px dashed $yellow;
	@include bor-rad(5px);
	overflow: hidden;
	margin-bottom: 10px;
	@include media-breakpoint-down(md) {
		display: none; }
	ul {
		li {
			width: 100%;
			&:before {
				background: lighten($red,50%); }
			a {
				display: block;
				color: $main;
				padding: 0px 15px;
				line-height: 36px;
				// text-transform: uppercase
				@include fs(13rem);
				// font-weight: 600
				&:hover {
					color: $red; } }
			&.active {
				a {
					font-weight: 600;
					color: $white;
					background: $red; } } } } }

