					@include media-breakpoint-down(xs) {
						padding-left: 0;
						width: 100%; }
					.product-info {
						float: left;
						@include media-breakpoint-down(sm) {
							width: 100%;
							float: none; } }
					.product-name {
						display: inline-block;
						@include fs(13rem);
						text-transform: uppercase;
						// border-left: 3px solid $red
						// padding-left: 10px
						margin-bottom: 5px;
						@include media-breakpoint-down(xs) {
							display: block; }
						a {
							color: $red;
							@include fs(20rem);
							font-weight: bold; } }
					.size {
						color: darken($white,60%);
						@include media-breakpoint-down(xs) {
							display: block; }
						&:before {
							content: '|';
							margin: 0 10px;
							@include media-breakpoint-down(xs) {
								display: none; } } }
					.product-category {
						@include fs(13rem);
						text-transform: uppercase;
						color: darken($white,40%);
						margin-bottom: 17px;
						// width: 100%
						@include media-breakpoint-down(sm) {
							margin-bottom: 5px; } }
					.product-price {
						margin-bottom: 5px;
						@include media-breakpoint-down(xs) {
							margin-bottom: 15px; }
						.old {
							display: inline-block;
							@include fs(14rem);
							color: darken($white,40%);
							text-decoration: line-through;
							font-style: italic; }
						.new {
							display: inline-block;
							@include fs(18rem);
							font-weight: 700;
							margin-left: 5px;
							color: $red;
							@include media-breakpoint-down(sm) {
 } } }								// +fs(24rem)
					.button {
						float: right;
						@include media-breakpoint-down(sm) {
							width: 100%;
							float: none; } }
					.btn-addtocart {
						display: inline-block;
						padding: 7px 20px;
						color: $white;
						@include fs(13rem);
						background: $red;
						text-transform: uppercase;
						@include transition(all,0.5s);
						@include media-breakpoint-down(xs) {
							width: calc(100% - 42px); }
						&:before {
							content: '\f07a';
							font-family: 'fontawesome';
							margin-right: 5px; }
						&:hover {
							background: darken($red,10%); } }
					.btn-removecart {
						display: inline-block;
						margin-left: 5px;
						width: 33px;
						height: 33px;
						background: darken($white,10%);
						@include transition(all,0.5s);
						&:hover {
							background: darken($white,20%);
							color: $red; } } } } } } }
.cart-info {
	border: 1px solid darken($white,20%);
	@include bor-rad(5px);
	height: 100%;
	.title {
		font-weight: bold;
		text-transform: uppercase;
		color: $bluegrey;
		padding: 10px;
		border-bottom: 1px solid darken($white,10%); }
	.cart-detail {
		padding: 10px;
		.form-group {
			@include flexbox;
			flex-wrap: wrap;
			@include fs(13rem);
			&:last-child {
				margin-bottom: 0; }
			.label {
				width: 150px;
				font-weight: bold; }
			.value {
				width: calc(100% - 150px); } } } }
