.page-content {
	.pages {
		.pagination {
			margin: 15px 0; } }
	.news-list {
		// border-bottom: 1px solid darken($white,10%)
		article {
			margin-bottom: 30px; } }
	.news-col {
		// border-bottom: 1px solid darken($white,10%)
		figure {
			.news-img {
				// width: 45%
				// float: left
				display: block;
				@include pos(rel,null);
				overflow: hidden;
				img {
					width: 100%;
					@include transition(all,0.5s); } }
			figcaption {
				// width: calc(100% - 45%)
				// float: left
				padding: 15px 0;
				@include pos(rel,null);
				time {
					display: block;
					@include pos(abs, 15px null null -20px);
					@include transform(translateX(-100%));
					background: $yellow;
					color: $white;
					padding: 5px 10px;
					@include fs(13rem);
					font-weight: 700;
					@include media-breakpoint-down(sm) {
						@include pos(static,null);
						@include transform(translateX(0%));
						margin-bottom: 10px;
						display: inline-block; } }
				.news-name {
					@include fs(16rem);
					font-weight: 500;
					text-transform: uppercase;
					line-height: 1.5;
					margin-bottom: 10px;
					a {
						color: $red;
						&:hover {
							color: $red; } } }
				.news-des {
					color: darken($white,60%);
					margin-bottom: 20px;
					@include fs(14rem);
					@include media-breakpoint-down(lg) {
						// max-height: 45px
 }						// overflow: hidden
					@include media-breakpoint-down(md) {
 } }						// max-height: unset
				.link {
					// font-weight: 700
					// text-transform: uppercase
					color: darken($white,60%);
					&:after {
						content: '\f105';
						font-family: 'fontawesome';
						margin-left: 5px; }
					&:hover {
						color: $yellow;
						letter-spacing: 1px; } } }
			&:hover {
				.news-img {
					img {
 } }						// +transform(scale3d(1.1,1.1,1))
				figcaption {
					.news-name {
						a {
 } } } } } } }							// color: $red

.othertitle {
	@include fs(16rem);
	font-weight: 500;
	border-left: 3px solid $red;
	padding-left: 10px;
	margin-bottom: 20px; }
.other-news {
	padding-top: 20px;
	.newslist {
		.news {
			border-bottom: 1px dashed darken($white,10%);
			padding: 10px 0;
			figure {
				display: flex;
				flex-wrap: wrap;
				.newsimg {
					width: 80px;
					img {} }
				figcaption {
					width: calc(100% - 80px);
					padding-left: 15px;
					.newsname {
						font-weight: 500;
						margin-bottom: 5px;
						a {
							color: $red;
							&:hover {
								color: $yellow; } } }
					time {
						display: inline-block;
						background: darken($white,10%);
						@include fs(13rem);
						font-weight: 500;
						color: $main;
						padding: 0px 5px; } } } } } }
