.product-col {
	@include bor-rad(10px);
	@include transition(all,0.5s);
	&:hover {
		box-shadow: -3px 3px 10px color-rgba($black,0.2); }
	figure {
		padding: 15px;
		.product-img {
			display: block;
			@include pos(rel,null);
			img {
				width: 100%; }
			.promotion {
				.sale {
					@include pos(abs, null 0 0 null);
					span {
						@include fs(14rem);
						font-weight: 700;
						color: $red;
						background: $yellow;
						@include bor-rad(5px);
						padding: 2px 5px; } } } }
		figcaption {
			padding-top: 10px;
			.product-name {
				a {
					display: block;
					@include fs(14rem);
					color: $main;
					font-weight: 700; } }
			.product-des {
				@include fs(13rem);
				color: darken($white,40%); }
			.product-price {
				margin-top: 10px;
				border-top: 1px solid darken($white,10%);
				padding: 5px 0;
				.new {
					@include fs(18rem);
					font-weight: 700;
					color: $red;
					float: left; }
				.old {
					@include fs(13rem);
					float: right;
					margin-top: 5px;
					text-decoration: line-through;
					color: darken($white,40%); } }
			.btn-wrap {}
			.btn-addtocart {
				display: block;
				line-height: 36px;
				@include fs(14rem);
				text-transform: uppercase;
				color: $white;
				background: $red;
				@include bor-rad(5px);
				text-align: center;
				&:hover {
					background: lighten($red,15%); } } } } }
.wishlist {
	.product-list {
		.product-col {
			figure {
				figcaption {
					.btn-wrap {
						@include flexbox;
						.btn-addtocart {
							width: calc(100% - 46px); }
						.btn-remove {
							width: 36px;
							height: 36px;
							line-height: 36px;
							background: darken($white,5%);
							color: $main;
							text-align: center;
							@include fs(18rem);
							@include bor-rad(5px);
							margin-left: 10px;
							&:hover {
								color: $white;
								background: darken($white,40%); } } } } } } } }
.product-sidebar {
	width: 20%;
	@include media-breakpoint-down(lg) {
		width: 25%; }
	@include media-breakpoint-down(md) {
		width: 0; } }
.product-page {
	width: calc(100% - 20%);
	background: $white;
	padding-left: 15px;
	// border-left: 1px solid darken($white,10%)
	overflow: hidden;
	@include media-breakpoint-down(lg) {
		width: 75%; }
	@include media-breakpoint-down(md) {
		width: 100%;
		border-left: none; }
	.product-sort {
		padding: 10px 0;
		.pagetitle {
			@include fs(18rem);
			font-weight: 500;
			text-transform: uppercase;
			float: left;
			margin-bottom: 0;
			line-height: 30px;
			@include media-breakpoint-down(xs) {
				@include fs(16rem); } }
		.amount {
			float: left;
			@include fs(13rem);
			text-transform: none;
			color: darken($white,40%);
			padding-left: 15px;
			margin-left: 15px;
			line-height: 30px;
			@include pos(rel,null);
			@include media-breakpoint-down(xs) {
				display: none; }
			&:before {
				content: '';
				display: block;
				width: 1px;
				height: 20px;
				background: darken($white,20%);
				@include pos(abs, 5px null null 0); } }
		.sorttype {
			float: right;
			@include pos(rel,null);
			@include media-breakpoint-down(xs) {
				float: left;
				clear: both;
				padding-top: 0; }
			&:after {
				content: '\f107';
				font-family: 'fontawesome';
				@include pos(abs, 3px 8px null null);
				@include media-breakpoint-down(xs) {
					top: 3px; } }
			select {
				color: darken($white, 60%);
				@include fs(14rem);
				border: 1px solid darken($white,20%);
				@include bor-rad(3px);
				padding: 6px 15px;
				-webkit-appearance: none;
				-moz-appearance: none;
				appearance: none; } } }
	.product-list {}
	.pages {
		margin: 20px 0; } }
.pages,.modulepager {
	width: 100%;
	.pagination {
		margin: 0;
		padding: 0;
		li {
			display: inline-block;
			+ li {
				margin-left: 3px; }
			a, span {
				display: block;
				width: 30px;
				line-height: 30px;
				text-align: center;
				@include bor-rad(5px);
				color: $main;
				border: 1px solid darken($white,10%);
				margin: 0;
				&:hover {
					color: $red;
					border-color: lighten($red,10%);
					background: lighten($red,60%); } }
			&.active {
				a, span {
					background: $red;
					color: $white;
					border-color: $red; } } } } }
.productdetailpage, .product-detail-page {
	.pages {
		display: none; }
	.product-page {
		width: 100%;
		border: none;
		padding: 0;
		@include media-breakpoint-down(lg) {
			padding: 10px;
			margin-bottom: 70px; }
		@include media-breakpoint-down(md);
		.slidewrap {
			text-align: center; }
		.share {
			text-align: center;
			margin-left: 20%;
			@include media-breakpoint-down(sm) {
				margin-left: 0;
				margin-bottom: 20px; } }
		.btn-wishlist {
			display: block;
			line-height: 40px;
			text-align: center;
			color: $main;
			margin-left: 20%;
			margin-bottom: 40px;
			.fa {
				color: $red; }
			&:hover {
				color: $red;
				letter-spacing: 1px; } } } }
.product-nav {
	width: 20%;
	// padding: 15px 15px
	@include media-breakpoint-down(xs) {
		width: 100%;
		order: 2;
		margin-bottom: 10px; }
	.slick-prev, .slick-next {
		width: 100%;
		@include fs(0);
		text-align: center;
		padding: 5px 0;
		background: darken($white,3%);
		@include bor-rad(5px);
		&:before {
			content: '';
			font-family: 'fontawesome';
			line-height: 1;
			@include fs(18rem); }
		&:hover {
			background: darken($white,10%); } }
	.slick-prev {
		&:before {
			content: '\f106'; } }
	.slick-next {
		&:before {
			content: '\f107'; } }
	.item {
		padding: 2px 0;
		.img {
			border: 1px solid transparent;
			@include transition(all,0.3s);
			display: flex;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			@include bor-rad(5px);
			overflow: hidden;
			img {
				max-width: 90%;
				max-height: 90%; } }
		&.slick-current {
			.img {
				border: 1px solid $red; } } } }
.product-slide {
	width: 80%;
	@include media-breakpoint-down(xs) {
		width: 100%;
		order: 1; }
	.item {
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				max-width: 100%;
				max-height: 100%; } } } }
.product-fly-button {
	@include pos(fix, null 0 0 0);
	z-index: 50;
	background: lighten($yellow,35%);
	// box-shadow: 0 -3px 20px color-rgba($black,0.1)
	border-top: 2px solid $red;
	@include transform(translateY(100%));
	@include transition(all,0.5s);
	@include media-breakpoint-down(md) {
		@include transform(translateX(0));
		.container {
			width: 100%;
			max-width: 100%;
			padding: 0; } }
	&.open {
		@include transform(translateY(0%)); }
	.product-info {
		float: left;
		margin: 0;
		@include media-breakpoint-down(md) {
			display: none; }
		.product-brand, .product-id {
			margin: 0;
			padding: 0;
			@include media-breakpoint-down(lg);
			@include media-breakpoint-down(md); }
		.product-id {
			margin-left: 15px; }
		.product-page-name {
			@include fs(20rem);
			border: none;
			margin: 0;
			margin-top: 20px;
			padding: 0; } }
	.colright {
		float: right;
		@include media-breakpoint-down(md) {
			float: none; }
		.product-price {
			float: left;
			text-align: right;
			line-height: 1.3;
			margin-top: 13px;
			@include media-breakpoint-down(md) {
				display: none; }
			.old {
				@include fs(13rem);
				color: darken($white,60%);
				text-decoration: line-through;
				strong {
					color: $main; } }
			.new {
				@include fs(24rem);
				font-weight: 500;
				color: darken($red,10%); } }
		.btn-buy {
			display: inline-block;
			background: $red;
			color: $white;
			margin-top: 15px;
			margin-bottom: 15px;
			margin-left: 20px;
			@include fs(16rem);
			padding: 8px 25px;
			@include bor-rad(5px);
			text-transform: uppercase; } } }
