.product-promotion {
	border-top: 1px dashed darken($white,20%);
	padding-top: 15px;
	label {
		display: inline-block;
		background: $white;
		@include fs(14rem);
		font-weight: 500;
		margin-bottom: 10px; }
	.promotion {
		display: block;
		margin-bottom: 10px; } }

.product-description {
	.titlewrap {}
	.title {
		@include fs(18rem);
		font-weight: 500;
		color: $main;
		padding: 10px 0;
		margin-bottom: 20px;
		&:after {
			content: '';
			display: block;
			width: 60px;
			height: 2px;
			background: $red;
			margin-top: 10px; } }
	.btn-comment {
		float: left;
		line-height: 30px;
		@include bor-rad(3px);
